import React, { Component } from 'react';
import firebase from './firebase';
import Header from './Header';
import NoteInput from './NoteInput';
import Notes from './Notes';
import './style.css';

//Importing all relevant Components to the main App.js file

//App is refactored from a function into a class

class App extends Component {

  //Using the constructor in order to initialize notes's state in the App class, we  assign notes an empty array and assigning state to notes, we must mount the instance of our component 
  constructor() {
    super();
    
    this.state = {
      notes: []
  //notes initial state
    }
  }


    //We must load data from firebase, we instantiate the network request here to load the data from firebase, and also we call this.changeDetection()
    componentDidMount() {
      this.dbRef = firebase.database();
  
      this.changeDetection();
    }
  



  changeDetection() {
    this.dbRef.ref('notes').on('child_added', snapshot => {
  //When dbRef.ref().on() takes a snapshot of notes when a child is added to the root notes located at the top of the database, allowing the user to type.
  //every time a child is added, the note is pushed and so a new note is added.
      let noteValue = {
        fullNote: snapshot.key,
        title: snapshot.val().title,
        note: snapshot.val().note
      }

      let notes = this.state.notes;
      notes.push(noteValue);

      this.setState({
        notes: notes
      });

    });
  // Same idea as the .ref().on() above, but a child is removed.
    this.dbRef.ref('notes').on('child_removed', snapshot => {

      let notes = this.state.notes;
      notes = notes.filter(note => note.fullNote !== snapshot.key);
  //Storing notes into a variable that calls on the state of notes, then reassigning notes with .filter() with an arrow function

      this.setState({
        notes: notes
      });
  //Setting the state to the notes variables we just assigned

    });
  }
  

  //Using JSX to print my components to the page
  
  render() {

    return (
      <div className="app">
        <Header />
        <main>
          <NoteInput />
          <Notes notes={ this.state.notes }/>
        </main>
      </div>
    );
  }
}

export default App;